import mongoose from'mongoose'
let Schema = mongoose.Schema;

let OrdersSchema = new Schema({
  'orderNo': {
    type: String,
    required: true,
    unique: true
  },  // ""
  'title':  String, // ""
  'price':  Number, // ""
  'orderNum':  Number, // ""
  'externalSysNum':  String, // ""
  'productAttrs':  String,  // ""
  'packageInfo':  String, // ""
  'remark':  String, // ""
  'orderStatus':  String, // ""
  'productCode':  String, // ""
  'createDate':  String, // ""
  'platform':  String, // ""
  'relativeOrder':  Array, // ""
});

// 
OrdersSchema.index({ createDate: -1});

export {
  OrdersSchema,
}



