import mongoose from'mongoose'
let Schema = mongoose.Schema;

let ProductSchema = new Schema({
  'productCode': {
    type: String,
    required: true,
    unique: true
  },  // ""
  'productName':  {
    type: String,
    required: true,
    unique: true
  }, // ""
  'price':  Number, // ""
  'stock':  Number, // ""
});

export {
  ProductSchema,
}



