let resultCodeMap = {
  'WL-0000': "",
  'WL-0001': "",
  'WL-0002': "",
  'WL-0006': "",
  'WL-0008': "",
  'WL-0010': "",
  'WL-0014': "",
}

class Result {
  constructor({code, msg, data}){
    this.resultCode = code
    this.resultResc = msg
    this.resultData = data || null
  }
}


class SuccessResult extends Result{
  constructor({msg, data}){
    super({
      code: 'WL-0000',
      msg: msg ? msg : '',
      data: data
    })
  }
}

class ErrorResult extends Result{
  constructor({code, msg}){
    super({
      code: code ? 'WL-' + code : "WL-0001",  // WL-0001
      msg: msg ? msg : '',
      data: null
    })
  }
}


export {
  Result,
  SuccessResult,
  ErrorResult,
}



